.SecondScreen {
  position: fixed;
  width: 100vw;
  height: 100%;

  animation: fadein;
  animation-duration: 5s;
}

.SecondScreen_foodSection {
  height: 60%;
  padding: 10%;
}
.SecondScreen_foodPicture {
  height: 100%;
  border: 7px dashed black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.SecondScreen_descriptionSection {
  height: 40%;
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 3%;
  padding-bottom: 13%;
}

.SecondScreen_description {
  height: 100%;
  border: 7px dashed black;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
